<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 3
      h1 Bases conceptuales de muestreo

    .row.justify-content-center.align-items-center.mb-5(data-aos="flip-down")
      .col-lg-10
        .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/temas/tema3/img1.png')})`}"
          )(alt="Imgen decorativa")
          .bloque-texto-g__texto.p-4
            p.mb-0 La actividad de anlisis de muestras de agua se enfoca en la necesidad de mantener un monitoreo constante de los recursos hdricos, ya que las actividades del ser humano generan transformaciones ambientales que si no se miden no se lograran dimensionar ni controlar.  

    .row.mt-5
      p A continuacin, se presenta un video que nos introduce las bases conceptuales de muestreo y medicin.

      figure(data-aos="flip-up")
        .video
          iframe(width="560" height="315" src="https://www.youtube.com/embed/ivfkDtPwXk4" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    separador
    #t_3_1.titulo-segundo.color-acento-botones(data-aos="fade-right")
       h2 3.1 Tipos y tcnicas de muestreo

    .row.justify-content-center.align-items-center 
      .col-8.col-lg-3(data-aos="fade-down-right")
        figure.mb-3
          img(src='@/assets/curso/temas/tema3/img2.png', alt='Imgen decorativa')  
      .col-lg-9
        p(data-aos="fade-down-left") Los tipos y tcnicas de muestreo varan de acuerdo con la situacin especfica y segn los objetivos previstos; algunos estudios requieren solamente #[b muestras instantneas o simples], mientras que en otros se necesita disponer de #[b muestras compuestas] o an ms elaboradas en tiempo y espacio. 

        .cajon.color-primario.p-4(data-aos="flip-down")
          p Muchas de las generalidades referentes a las tcnicas de muestreo y conservacin, se encuentran plasmadas en las Normas Tcnicas Colombianas [NTC] 5667-2 y 5667-3.   

        p.mt-3(data-aos="fade-up-left") En el caso de anlisis microbiolgicos bsicamente incluyen procedimientos para prevenir su contaminacin durante el llenado de los recipientes por microorganismos presentes en el exterior de los grifos, accesorios, en el ambiente o en el operario. 

    .row.bg1-tema3.align-items-center(data-aos="fade-right")
      h4.text-white.mt-2 Tipos de muestras 

    .row.justify-content-center.align-items-center.mt-5
      .col-6.col-lg-1(data-aos="fade-down-right")
        figure
          img(src='@/assets/curso/temas/tema3/img3.svg', alt='Imgen decorativa')  
      .col-lg-11(data-aos="fade-up-left")
        p Antes de iniciar el muestreo, es elemental tener claramente definido la forma como sern tomadas las muestras para poder realizar un trabajo de campo; Segn el Instituto Colombiano de Normas Tcnicas y Certificacin [Icontec] (1995) en la norma tcnica colombiana <b>[NTC] 5667-2 Tcnicas generales de muestreo</b>, dentro de los tipos de muestras se pueden encontrar: sencillas, compuestas, peridicas, continuas y de serie (pp.2-8). 

    .row.mt-5
      p Ahora, se mencionan algunas de ellas:

      .tarjeta.tarjeta--azul2.p-4
        SlyderA(tipo="b").p-4
          .row
            .col-md-6.mb-4.mb-md-0
              p
                b Muestras simples
              p Este tipo de muestra se toma en un momento y lugar determinado. Es aplicable en los casos que se sabe, la composicin del agua no vara significativamente en el tiempo o lugar, como lo son usualmente para caracterizacin fisicoqumica de aguas naturales. En este caso, tomar diferentes muestras puntuales puede ser til para describir el comportamiento a travs del espacio y tiempo (NTC 5667-2, 1995).

            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema3/img4.png', alt='Imgen decorativa')

          .row
            .col-md-6.mb-4.mb-md-0
              p
                b Muestras compuestas

              p Debido a la variacin horaria de su caudal, por tal razn son muy utilizadas en el monitoreo de ros, vertimientos o procesos industriales en lnea. Su toma se realiza en un mismo punto en intervalos de tiempo previamente definidos, y el volumen de cada muestra parcial puede ser constante o variar en funcin del caudal.  Para los casos en que se requiere variar el volumen en cada toma, se recolectan muestras parciales (alcuotas) ej.: cada 2 o 3 horas, cuyo volumen se obtiene segn la siguiente relacin:  

              .row.justify-content-center.mt-3
                .col-8.col-lg-4
                  figure
                    img(src='@/assets/curso/temas/tema3/img8.svg', alt='Imgen decorativa')

            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema3/img5.png', alt='Imgen decorativa')
              
          .row
            .col-md-6.mb-4.mb-md-0
              p 
                b Muestras integradas

              p Este tipo de muestra consiste en la recoleccin de muestras simples en diferentes puntos de manera simultnea que posteriormente son mezcladas. En caso de presentarse variaciones de caudal en los puntos de muestreo, se debe tener en cuenta para que el volumen aportado sea proporcional al caudal medido durante la toma de la muestra. (Instituto de Hidrologa, Meteorologa y Estudios Ambientales [IDEAM], 2017).

            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema3/img6.png', alt='Imgen decorativa')

          .row
            .col-md-6.mb-4.mb-md-0
              p 
                b Muestras en serie

              p Dentro de las muestras en serie se distinguen dos tipos, de perfil profundo y de rea:

              p <b>-De perfil profundo:</b> muy aplicables en oceanografa cuando el objetivo es conocer la variacin vertical de un parmetro, por ejemplo, definir la posicin de la capa termohalina, de la picnoclina, etc.

              p <b>-De perfil de rea:</b> serie de muestras de agua tomadas a una profundidad en particular, de una masa de agua en diversas locaciones; muy utilizadas para definir distribuciones espaciales por capas. (Invemar, 2013, p. 14).

            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema3/img7.png', alt='Imgen decorativa')
                

      p.mt-5 La norma NTC ISO 5567-2, en el numeral 4 indica los tipos de muestras segn procedimientos estandarizados, consultarla en la base de datos del SENA.    


    .row.bg1-tema3.align-items-center(data-aos="fade-right")
      h4.text-white.mt-2 Tcnicas de muestreo 

    .row
      p.mt-5 Las cuales se subdividen en tres:

    .row.justify-content-center.mb-5
      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img9.svg", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-3.text-center Muestreo manual
            p.mb-0.text-center Se realiza para sitios de fcil acceso o aquellos que con ciertas adaptaciones puedan facilitar la toma de muestras. Permite al encargado de la toma, observar los cambios en las caractersticas del agua en cuanto a sustancias flotantes, color, olor, aumento o disminucin de caudales, entre otros (2011, p. 21).

      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img10.svg", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-3.text-center Muestreo automtico
            p.mb-0.text-center En lugares de difcil acceso se recomiendan muestreadores automticos, son precisos, aunque su desventaja es el montaje y calibracin del equipo; su aplicacin implica instalacin de equipos (antenas, paneles solares, etc.) y herramientas (licencias de trasmisin, #[i software]) elevando  costos y  limitando su implementacin (INS, 2011, pp. 21-22).

      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img11.svg", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-3.text-center Muestreo mixto
            p.mb-0.text-center Segn el INS (2007) se operan programas de muestreo mixto, convirtiendo el monitoreo en un sistema integrado que permite la verificacin manual de los resultados obtenidos de forma automtica. Esta se realiza aleatoriamente, de tal manera que se pueda realizar calibracin, ajuste y mantenimiento de los equipos automticos (p.22). 

    .row.justify-content-center.align-items-center.mt-5
      .col-6.col-lg-1(data-aos="fade-down-right")
        figure.mb-3
          img(src='@/assets/curso/temas/tema3/img12.svg', alt='Imgen decorativa')
      .col-11(data-aos="fade-up-left")
        p Las tcnicas del muestreo de agua para #[b anlisis microbiolgicos] bsicamente incluyen procedimientos para prevenir su contaminacin durante el llenado de los recipientes por microorganismos presentes en el exterior de los grifos, accesorios, en el ambiente o en el operario. Las tcnicas de llenado de los recipientes se pueden dividir en dos: #[b muestreo en el grifo o muestreo por inmersin.]

    .row.bg1-tema3.align-items-center.mt-5(data-aos="fade-right")
      h4.text-white.mt-2 Muestreos en el grifo

    .row.justify-content-center.align-items-center.mt-5
      .col-8.col-lg-3(data-aos="fade-down-right")
        figure.mb-3
          img(src='@/assets/curso/temas/tema3/img13.png', alt='Imgen decorativa')  
      .col-lg-9(data-aos="fade-up-left")
        p Este tipo de muestreo se aplica especficamente para las muestras de #[b agua potable de llave]. Estas pueden ser tomadas en plantas de tratamiento y tanques de almacenamiento, agua en la red de distribucin, agua del grifo del consumidor, agua para consumo, agua de manantiales o pozos que cuenten con dispositivos de tomas de muestra instalados permanentemente.

        p Cuando se va a realizar la toma de estas muestras se debe hacer una previa desinfeccin del grifo, excepto para la toma de muestras de agua de consumo directo, ya que la calidad microbiolgica de esta agua depender tanto de los microorganismos presentes en el agua, como de los presentes en los accesorios y el grifo.

    .row.mt-5(data-aos="fade-right")
      h4.color-titulo-azul Tcnica de llenado para muestreo en el grifo

    .row.mt-4(data-aos="fade-left")
      p Esta tcnica debe seguir los protocolos establecidos, as como lo indica el Instituto Colombiano de Normas Tcnicas y Certificacin [ICONTEC] (2015) en la norma tcnica colombiana [NTC] 6151, calidad del agua. Muestreo para anlisis microbiolgico:

    .row.mt-5.justify-content-center.align-items-center 
      .col-lg-8
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row(titulo="Norma 1")
            .col-md-8.mb-4.mb-md-0
              p Se deben retirar todos los dispositivos y elementos colocados en el dispositivo de muestreo, en el caso de hacer muestreo para agua de consumo, este paso se debe omitir.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img14.png', alt='Imgen decorativa')

          .row(titulo="Norma 2")
            .col-md-8.mb-4.mb-md-0
              p La persona que tome la muestra deber mantener las tcnicas aspticas para prevenir la contaminacin de la muestra, para ello deber lavarse las manos, ponerse tapabocas y usar guantes estriles.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img15.png', alt='Imgen decorativa')

          .row(titulo="Norma 3")
            .col-md-8.mb-4.mb-md-0
              p Se debe limpiar el grifo o dispositivo con un pao hmedo para retirar la suciedad visible.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img16.png', alt='Imgen decorativa')

          .row(titulo="Norma 4")
            .col-md-8.mb-4.mb-md-0
              p Una vez hecho el procedimiento de limpieza se debe flamear el grifo con un mechero de alcohol. Esto solo se debe hacer si el grifo y las tuberas anexas son metlicas.

              p Recuerde que el paso de desinfeccin no se debe realizar para el muestreo de agua para consumo.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img17.png', alt='Imgen decorativa')

          .row(titulo="Norma 5")
            .col-md-8.mb-4.mb-md-0
              p Una vez limpio y desinfectado el grifo se debe dejar drenar el agua por 1 a 3 minutos hasta que el agua en las tuberas haya sido renovada y la temperatura se haya estabilizado.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img18.png', alt='Imgen decorativa')


          .row(titulo="Norma 6")
            .col-md-8.mb-4.mb-md-0
              p Para hacer el llenado del recipiente, se debe retirar el protector que est cubriendo la tapa del recipiente, destapar el frasco y hacer el llenado manteniendo la esterilidad de la tapa, para ello puede colocar la tapa en el papel estril que estaba cubriendo el recipiente.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img19.png', alt='Imgen decorativa')

            
          .row(titulo="Norma 7")
            .col-md-8.mb-4.mb-md-0
              p Llenar directamente el recipiente estril hasta dejar un espacio de aire que facilitar el mezclado, una vez se obtiene el volumen requerido se tapa inmediatamente el recipiente sin tocar la boca del frasco o el interior de la tapa.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img20.png', alt='Imgen decorativa')

          .row(titulo="Norma 8")
            .col-md-8.mb-4.mb-md-0
              p Se debe rotular para asegurar que la informacin del rtulo corresponda al punto, fecha y hora en la que se realiz el muestreo.
          
            .col-md-4
              figure
                img(src='@/assets/curso/temas/tema3/img21.png', alt='Imgen decorativa')
      
       
      .col-6.col-lg-4(data-aos="fade-down-left")
        figure
          img(src='@/assets/curso/temas/tema3/img22.png', alt='Imgen decorativa')

    .row.bg1-tema3.align-items-center.mt-5(data-aos="fade-right")
      h4.text-white.mt-2 Muestreo por inmersin

    .row.justify-content-center.align-items-center.mt-5
        
      .col-lg-12(data-aos="fade-up-left")
        p Se realiza cuando se requiere tomar muestras de profundidad por ejemplo, en el caso de muestreos de tanques de almacenamiento que no cuenten con dispositivos de muestras o en el agua de manantiales, pozos o  piscinas.

        p En estos casos la muestra se debe tomar con un recipiente estril en su interior y exterior. Tan pronto se va a tomar la muestra se debe retirar la envoltura protectora del recipiente y con la ayuda de una vara de muestreo se lleva a cabo la toma de muestra.

        p Revise el siguiente contenido para conocer indicaciones especficas de acuerdo al tipo de muestra que se vaya a recolectar:

    .row.mt-5
      .tarjeta.tarjeta-amarilla4.p-4
        LineaTiempoC.color-acento-botones
          .row(titulo="1")
            .col-md-6
              figure.mb-3
                img(src='@/assets/curso/temas/tema3/img24.png', alt='Imgen decorativa')
              
            .col-md-6.mb-4.mb-md-0
              p En el caso de la toma de las piscinas, la muestra se debe tomar de -10 cm a -20 cm de profundidad y el recipiente se introduce horizontalmente para evitar la prdida de tiosulfato. Para balnearios se hace de manera similar haciendo la toma a -20 cm a -30 cm.

            .row.justify-content-center.align-items-center.mt-5
              .col-lg-6
                TarjetaAudio.color-acento-botones.mb-3(
                  texto=" "
                  :audio="require('@/assets/componentes/audios/muestreo-piscinas.mp3')"
                  @audio-hover="mostrarIndicadorTarjetaAudio = false"
                  )


          .row(titulo="2")
            .col-md-6
              figure.mb-3
                img(src='@/assets/curso/temas/tema3/img25.png', alt='Imgen decorativa')
              
            .col-md-6.mb-4.mb-md-0
              p En el caso de pozos o manantiales sin dispositivos de muestreo, la toma se debe hacer con bombas o dispositivos sumergibles estriles.

            .row.justify-content-center.align-items-center.mt-5
              .col-lg-6
                TarjetaAudio.color-acento-botones.mb-3(
                  texto=" "
                  :audio="require('@/assets/componentes/audios/muestreo-pozos_o_manantiales.mp3')"
                  @audio-hover="mostrarIndicadorTarjetaAudio = false"
                  )

          .row(titulo="3")
            .col-md-6
              figure.mb-3
                img(src='@/assets/curso/temas/tema3/img26.png', alt='Imgen decorativa')
              
            .col-md-6.mb-4.mb-md-0
              p En el caso de aguas profundas en el mar, lagos o ros e incluso de aguas residuales que sern analizadas microbiolgicamente, es recomendable usar dispositivos especializados como el muestreador ZoBell J-Z.

            .row.justify-content-center.align-items-center.mt-5
              .col-lg-6
                TarjetaAudio.color-acento-botones.mb-3(
                  texto=" "
                  :audio="require('@/assets/componentes/audios/muestreo-aguas_profundas.mp3')"
                  @audio-hover="mostrarIndicadorTarjetaAudio = false"
                  )

      p.mt-5 Se invita a consultar informacin segn las normas aplicadas.

    .row.mt-3(data-aos="flip-down")
      .tarjeta.bgDescarga.p-3.mb-5
        .row.justify-content-around.align-items-center
          .col-3.col-sm-2.col-lg-1
            img(src="@/assets/curso/temas/tema3/img27.svg",alt="Imgen decorativa")
          .col
            .row.justify-content-between.align-items-center
              .col.mb-3.mb-sm-0
                h3.mb-1.texto-blanco Normas aplicadas
                p.texto-blanco Si es de su inters ampliar la informacin puede ir al material complementario y consultar el video sobre #[b las tcnicas de muestreo en aguas residuales.]
              .col-sm-auto
                a.boton.color-primario(href="https://www.youtube.com/embed/UOBmZqSftJY" target="_blank")
                  span.texto-negro <b>Link</b>
                  i.fas.fa-link.texto-negro
        
    .row.bg2-tema3-v2.align-items-center.mt-5(data-aos="fade-right")
      h4.text-white.mt-2 Tcnicas de Manos Limpias (ML)/Manos Sucias (MS)

    .row.justify-content-center.align-items-center.mt-5
      .col-8.col-lg-4(data-aos="fade-down-right")
        figure.mb-3
          img(src='@/assets/curso/temas/tema3/img28.png', alt='Imgen decorativa')
      
      .col-lg-8(data-aos="fade-up-left")
        p Esta es una tcnica sencilla que involucra el proceso de la toma de la muestra, sin embargo, es una estrategia que se ha incentivado por ser algo prctico, de fcil ejecucin y de buenos resultados de acuerdo con el Servicio Geolgico de los Estados Unidos [USGS], en su manual de campo interinstitucional para la recoleccin de datos sobre la calidad del agua (2000).

        p Los procedimientos de muestreo limpios, incluidas las tcnicas de ML/MS, son necesarios cuando se recogen muestras inorgnicas para determinar la presencia de metales y otros oligoelementos. Los procedimientos de muestreo limpio se recomiendan para todos los dems muestreos, en la medida en que resulte razonable, pero particularmente cuando el objeto analizado, podra estar sujeto a contaminacin en el terreno o de los procedimientos de laboratorio a un nivel que podra sobrepasar la demanda qumica de oxgeno (DQO) con fines de informacin e interpretacin.

    .row.justify-content-center.align-items-center.mt-3.mb-5
      .col-lg-8(data-aos="flip-down")
        .cajon.color-primario.p-4
          p Las tcnicas ML/MS separan las funciones en el terreno y dedican un individuo como <b>"manos limpias"</b> para tareas relacionadas con tener contacto directo con la muestra.

    .row.mb-5
      p  A continuacin, se presenta la figura que  resume estas tcnicas:

    .row.mb-5(data-aos="fade-left")
      .titulo-sexto.color-acento-contenido.mb-0
        h5 Figura 1
        br
        span Tcnicas  de Manos limpias / Manos sucias        
    ImagenInfografica.color-acento-contenido
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/temas/tema3/img29.svg', alt='Tcnicas de Manos limpias / Manos sucias. MS se encarga de todas las operaciones que relaciona con entrar en contacto con posibles fuentes de contaminacin; por ejemplo, MS: trabaja exclusivamente en el exterior de las cmaras del procesamiento y preservacin, prepara y opera el equipo de muestreo, incluidas las bombas y muestras discretas, el interruptor de bomba peristltica, el controlador de bombas y el sistema distribuidor. ML se encarga de todas las operaciones que se relacionan con equipos que entran en contacto con la muestra; por ejemplo, ml: maneja la botella de la muestra de agua de superficie, maneja el extremo de descarga del tubo o la lnea de la muestra de agua de superficie, transfiere la muestra al separador de embudo, prepara un espacio de trabajo limpio (dentro del vehculo), prepara las cmaras de procesamiento y preservacin.')

      .tarjeta.tarjeta--blanca.p-3(x="17%" y="30.3%" numero="")
        .h5.mb-2 
        p MS se encarga de todas las operaciones que relaciona con entrar en contacto con posibles fuentes de contaminacin; por ejemplo, MS: trabaja exclusivamente en el exterior de las cmaras del procesamiento y preservacin, prepara y opera el equipo de muestreo, incluidas las bombas y muestras discretas, el interruptor de bomba peristltica, el controlador de bombas y el sistema distribuidor.

      .tarjeta.tarjeta--blanca.p-3(x="85%" y="30.3%" numero="")
        .h5.mb-2
        p ML se encarga de todas las operaciones que se relacionan con equipos que entran en contacto con la muestra; por ejemplo, ml: maneja la botella de la muestra de agua de superficie, maneja el extremo de descarga del tubo o la lnea de la muestra de agua de superficie, transfiere la muestra al separador de embudo, prepara un espacio de trabajo limpio (dentro del vehculo), prepara las cmaras de procesamiento y preservacin.

    .row.justify-content-center.align-items-center.mb-5
      .col-10
        .tarjeta.tarjeta-azul5.p-3(data-aos="flip-down")
          .row.justify-content-center.align-items-center
            .col-4.col-lg-1
              figure.mb-3.mb-lg-0
                img(src='@/assets/curso/temas/tema3/img29-1.svg', alt='Imgen decorativa')          
            .col-lg-11
              p.mb-0.texto-blanco Tareas especficas se asignan al comienzo a ML o MS, algunas de ellas se superponen y pueden ser manejadas por una u otra persona siempre que no se introduzca contaminacin en las muestras.


    .row.bg1-tema3-v2.align-items-center.mt-5(data-aos="fade-right")
      h4.text-white.mt-2 Tcnica muestreo tomado al azar

    .row.justify-content-center.align-items-center.my-5
      .col-8.col-lg-4(data-aos="fade-down-right")
        figure.mb-3
          img(src='@/assets/curso/temas/tema3/img30.png', alt='Imgen decorativa')
      .col-lg-8(data-aos="fade-up-left")
        p La ltima tcnica a mencionar es la que se basa en una muestra que se toma totalmente al azar, por medio de un recipiente abierto: el cual se coloca en un solo punto en la superficie (arroyo, embalse, ro etc.), estas muestras por lo general se toman antes de hacer otro trabajo en el lugar para no afectar la corriente y la posterior recoleccin; para esta tcnica se utilizan muestreadores colectores que suelen pararse corriente abajo de la botella durante la operacin de llenado, evitando que entren partculas que estn suspendidas  (NTC 5667-2, 1995).


    .row.mb-5(data-aos="flip-down")
      .tarjeta.bgDescarga.p-4.mb-5
        .row.justify-content-around.align-items-center
          .col-3.col-sm-2.col-lg-1
            img(src="@/assets/curso/temas/tema3/img27.svg",alt="Imgen decorativa")
          .col
            .row.justify-content-between.align-items-center
              .col.mb-3.mb-sm-0
                p.texto-blanco Si es de su inters ampliar la informacin puede ir al material complementario y consultar el video sobre la #[b metodologa para realizar un muestreo integrado en un ro.]
              .col-sm-auto
                a.boton.color-primario(href="https://www.youtube.com/embed/JBu6RQ1QMD0" target="_blank")
                  span.texto-negro Enlace
                  i.fas.fa-link.texto-negro

    .row.bg2-tema3-v2.align-items-center.mb-5(data-aos="fade-right")
      h4.text-white.mt-2 Precauciones para la toma de la muestra en funcin de su origen.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8(data-aos="fade-up-right")
        p Estas recomendaciones son elementales a la hora de tomar muestras, la primera es enjuagar el envase por lo menos 2 o 3 veces con el agua a muestrear antes de tomar la muestra definitiva.

        p Las dems recomendaciones sugeridas se distribuyen teniendo la fuente de muestreo que pueden ser superficiales (ros, arroyos, canales, represas, lagos, aljibes) o subterrneas (pozos calzados o de balde, perforaciones), las cuales son atribuidas por el Instituto Nacional de Tecnologa Agropecuaria [INTA] (2011), en el protocolo de muestreo, transporte y conservacin de muestras de agua con fines mltiples de Argentina, pero que son aplicables a cualquier pas, a saber:
      
      .col-lg-4(data-aos="fade-down-left")
        figure
          img(src='@/assets/curso/temas/tema3/img31.svg', alt='Imgen decorativa')        

    .tarjeta.tarjeta--amarilla.p-5
      SlyderA(tipo="b")
        .row
          .col-md-6.mb-4.mb-md-0
            h4 Agua de red
            p Para la toma de una muestra de agua de red se abrir el grifo o canilla y se dejar que el agua corra el tiempo suficiente de manera de tener purgada toda la caera que llega desde el tanque, el ramal donde se encuentre el grifo debe ser el principal, proveniente de la red, y no debe estar conectado en el trayecto con otras caeras, filtros, ablandadores u otros artefactos que puedan alterar la calidad del agua del ramal principal.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema3/img32.svg', alt='Imgen decorativa')

        .row
          .col-md-6.mb-4.mb-md-0
            h4 Agua de perforaciones o pozos calzados
            p La muestra se debe tomar de la caera inmediata al pozo y es conveniente que, antes de proceder a la toma de la muestra, la impulsin se mantenga en marcha el tiempo suficiente que contemple la profundidad del o de los acuferos, hasta que el agua emerja clara (sin sedimentos ni restos vegetales) y que sea del acufero. Se debe prestar especial atencin a esto si el pozo estuviera en desuso. 

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema3/img33.svg', alt='Imgen decorativa')
            
        .row
          .col-md-6.mb-4.mb-md-0
            h4 Agua superficial proveniente de un curso de agua en movimiento
            p Debe ponerse especial atencin en buscar puntos estratgicos de muestreo (puentes, alcantarillas, botes, muelles), ya que se debe muestrear de sitios donde el agua se encuentre en circulacin. Nunca es recomendable muestrear desde donde se encuentra estancada.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema3/img34.svg', alt='Imgen decorativa')

        .row
          .col-md-6.mb-4.mb-md-0
            h4 Agua superficial proveniente de un espejo de agua (represa o lago)
            p En estos casos, se puede proyectar una jabalina a unos 2 metros de la orilla, para no muestrear del borde, evitando tomar la muestra de la capa superficial o del fondo. Sumergir el frasco en el agua (incorporando un peso) con el cuello hacia abajo hasta una profundidad de 15 a 30 cm, destapar y girar el frasco ligeramente permitiendo el llenado. Retirar el frasco despus que no se observe ascenso de burbujas.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema3/img35.svg', alt='Imgen decorativa')

        .row
          .col-md-6.mb-4.mb-md-0
            h4 Tanque de almacenamiento
            p Tomar la muestra bajando el frasco dentro del pozo hasta una profundidad de 15 a 30 cm desde la superficie libre del lquido, evitando en todo momento tocar las paredes del pozo. Cuando no es posible tomar la muestra directamente con la mano, debe atarse al frasco un sobrepeso usando el extremo de un cordel limpio o en su caso equipo muestreador comercial.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema3/img36.svg', alt='Imgen decorativa')


    separador
    #t_3_2.titulo-segundo.color-acento-botones(data-aos="fade-right")
       h2 3.2 Tamao de la muestra

    .row.mb-5
      .col-10.col-lg-4(data-aos="fade-down-right")
        figure.mb-4
          img(src='@/assets/curso/temas/tema3/img37.svg', alt='Imgen decorativa')  

      .col-lg-8
        p(data-aos="fade-down-left") Generalmente se recomienda trabajar con un volumen mnimo de 2 litros. Sin embargo, el volumen de muestra requerido puede variar en funcin del tipo y nmero de anlisis fisicoqumico a desarrollar, as como de las concentraciones de los analitos (INS, 2011, p.44). Particularmente el volumen requerido es determinado por el laboratorio teniendo en cuenta que el volumen base puede ser analizado por duplicado o triplicado.

        .cajon.color-primario.p-4.mb-3(data-aos="flip-down")
          p.mb-0 El volumen mnimo requerido para cada determinacin en laboratorio puede ser consultado en el #[i Standard Methods for the Examination of Water and Wastewater], en el captulo correspondiente a recoleccin de muestras (#[i Collection of Samples]).      

        p.mb-4(data-aos="fade-up-left") Es recomendable, tener en cuenta el uso de diferentes envases para la recoleccin del volumen de muestra teniendo en cuenta las condiciones de recoleccin y transporte en especfico. En todo caso, se requerir atender normas tcnicas y normatividad aplicable.

      p Para realizar el anlisis microbiolgico se recomienda tener en cuenta la cantidad de agua a muestrear, como se presenta en la siguiente tabla:  

    .titulo-sexto.color-acento-contenido(data-aos="fade-left")
      h5 Tabla 1
      br
      spam Tamao de muestra segn el tipo de agua a muestrear y ensayo a realizar

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10(data-aos="flip-left")
        .tabla-a.color-azul
          table(alt="Tabla 1. Tamao de muestra segn el tipo de agua a muestrear y ensayo a realizar, ensayo microbilgico, tipo de muestra y volumen de muestra.")
            thead
              tr
                th Ensayo microbiolgico
                th Tipo de muestra
