    .row.mb-5
      p Una vez definido el tamao de la muestra se sugiere revisar los siguientes pasos prcticos para la toma de la misma. Se debe recordar que estos pueden variar dependiendo del objetivo del muestreo, del sitio, presupuesto y elementos que se tengan a disposicin, para el INTA esos pasos son:

    ImagenInfografica.color-acento-botones
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/temas/tema3/img38.svg', alt='Imgen decorativa')  

      .tarjeta.tarjeta-amarilla.p-3(x="19.5%" y="45.3%" numero="1")
        .row.justify-content-center
          .col-4.col-lg-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/img39.svg', alt='Imgen decorativa')          
          
          p Cerrar el envase asegurando su cierre hermtico.

      .tarjeta.tarjeta-amarilla.p-3(x="26.7%" y="64%" numero="2")
        .row.justify-content-center
          .col-4.col-lg-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/img40.svg', alt='Imgen decorativa')          
          
          p Si no estaba rotulada la botella rotularla con tinta indeleble. Siempre tener papel y cinta adhesiva para emergencias o muestras no planificadas.

      .tarjeta.tarjeta-amarilla.p-3(x="38%" y="33%" numero="3")
        .row.justify-content-center
          .col-4.col-lg-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/img41.svg', alt='Imgen decorativa')          
          
          p Enjuagar 2 a 3 veces con la fuente de agua que se va a muestrear, desechando el agua de enjuague.

      .tarjeta.tarjeta-amarilla.p-3(x="40%" y="67%" numero="4")
        .row.justify-content-center
          .col-4.col-lg-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/img42.svg', alt='Imgen decorativa')          
          
          p Que el envase tenga una capacidad de por lo menos 1 litro.

      .tarjeta.tarjeta-amarilla.p-3(x="72%" y="11%" numero="5")
        .row.justify-content-center
          .col-4.col-lg-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/img43.svg', alt='Imgen decorativa')          
          
          p Si el envase est rotulado verificar que sea el correcto.

      .tarjeta.tarjeta-amarilla.p-3(x="73.4%" y="38.7%" numero="6")
        .row.justify-content-center
          .col-4.col-lg-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/img44.svg', alt='Imgen decorativa')          
          
          p Guardar la muestra en lugar fresco (interior de un vehculo) o en conservadora si fuera necesario y llevarla al laboratorio en el menor tiempo posible (se recomienda como tiempo mximo de entrega a laboratorio de 4 das) (2011, p.5).

      .tarjeta.tarjeta-amarilla.p-3(x="64%" y="57%" numero="7")
        .row.justify-content-center
          .col-4.col-lg-2.mb-3
            figure
              img(src='@/assets/curso/temas/tema3/img45.svg', alt='Imgen decorativa')          
          
          p Recoger la muestra sin dejar cmara de aire. Se puede dejar un mnimo sin llenar que permita la variacin de volumen debida a potenciales diferencias trmicas. Si se le va a agregar algn conservante contemplar el volumen necesario para el mismo.

    separador
    #t_3_3.titulo-segundo.color-acento-botones(data-aos="fade-right")
       h2 3.3 Tipos de ensayos

    .row.mb-5
      .col-10.col-lg-4(data-aos="fade-down-right")
        figure.mb-4
          img(src='@/assets/curso/temas/tema3/img46.svg', alt='Imgen decorativa')  

      .col-lg-8(data-aos="fade-up-left")
        p Los ensayos #[b #[i in situ]], son los ensayos o mediciones que realizan de algunos parmetros en campo, que por su naturaleza si no se realiza de manera inmediata los resultados arrojados posteriormente no sern verdicos, para agua de consumo humano se encuentran dentro de estos parmetros: la temperatura, el olor, el sabor, el cloro residual libre y el pH (INS, 2011, p.46); sin embargo,  para otros tipos de aguas como las residuales o las superficiales, estos tambin son aplicables para mediciones #[b #[i in situ]] incluyendo conductividad, turbidez y oxgeno disuelto, la medicin de estos se realiza con el equipo multiparamtrico.

        p Estas mediciones se realizan generalmente, haciendo uso de equipos porttiles como sondas multiparamtricas, pHmetros y conductmetros; pero, se debe resaltar que para hacer estos anlisis siempre se debe llevar a cabo bajo las indicaciones de revisin y calibracin de los equipos.

    .row.mb-5
      p A continuacin, de acuerdo con el (Invemar, 2013, p.24), se puede observar la explicacin de algunos parmetros medidos #[b #[i in situ:]]

    .row.mt-5  
      SlyderF(columnas="col-lg-6 col-xl-4")
        .tarjeta.tarjeta--naranja.p-4
          .row.justify-content-center.mb-3
            .col-7.col-lg-6
              img(src='@/assets/curso/temas/tema3/img47.svg' alt='Imgen decorativa')

          h4.text-center Oxgeno disuelto

          p.text-center Este parmetro proporciona una medida de la cantidad de oxgeno disuelto en el agua. Mantener una concentracin adecuada de oxgeno disuelto en el agua es importante para la supervivencia de los peces y otros organismos de vida acutica.

        .tarjeta.tarjeta--naranja.p-4
          .row.justify-content-center.mb-3
            .col-7.col-lg-6
              img(src='@/assets/curso/temas/tema3/img48.svg' alt='Imgen decorativa')

          h4.text-center Conductividad
 
          p.text-center En una muestra de agua es una medida de la capacidad que tiene la solucin para transmitir corriente elctrica. Esta capacidad depende de la presencia, movilidad, valencia y concentracin de iones, as como de la temperatura del agua.

        .tarjeta.tarjeta--naranja.p-4
          .row.justify-content-center.mb-3
            .col-7.col-lg-6
              img(src='@/assets/curso/temas/tema3/img49.svg' alt='Imgen decorativa')
          
          h4.text-center pH

          p.text-center Es una medida de la concentracin de iones de hidrgeno en el agua. Aguas fuera del rango normal de 6 a 9 pueden ser dainas para la vida acutica. Estos niveles de pH pueden causar perturbaciones celulares y la eventual destruccin de la flora y fauna acutica (Invemar, 2013, p.22).

        .tarjeta.tarjeta--naranja.p-4
          .row.justify-content-center.mb-3
            .col-8.col-lg-6
              img(src='@/assets/curso/temas/tema3/img50.svg' alt='Imgen decorativa')

          h4.text-center Temperatura
 
          p.text-center Juega un papel muy importante en la solubilidad de los gases, en la disolucin de las sales y por lo tanto en la conductividad elctrica, en la determinacin de pH, en el conocimiento del origen de agua y de las eventuales mezclas, etc. (Invemar, 2013, p.21).

        .tarjeta.tarjeta--naranja.p-4
          .row.justify-content-center.mb-3
            .col-7.col-lg-6
              img(src='@/assets/curso/temas/tema3/img51.svg' alt='Imgen decorativa')
          
          h4.text-center Turbidez

          p.text-center El agua turbia es provocada por la materia insoluble, en suspensin o dispersin coloidal. Es un fenmeno ptico que consiste esencialmente en una absorcin de luz combinado con un proceso de difusin (Invemar, 2013, p.26).

    separador
    #t_3_4.titulo-segundo.color-acento-botones(data-aos="fade-right")
       h2 3.4 Puntos de tomas de muestras

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4(data-aos="fade-down-right")
        figure.mb-4
          img(src='@/assets/curso/temas/tema3/img52.svg', alt='Imgen decorativa')  

      .col-lg-8(data-aos="fade-up-left")
        p El personal de monitoreo deber obtener toda la informacin posible y de manera detallada acerca de las estaciones del recurso hdrico a monitorear, generalmente esta informacin es proporcionada por el cliente, ello servir para planear todo el procedimiento de muestreo.

        b.mb-0 La ubicacin de los puntos de muestreo deber cumplir los siguientes criterios: 
        p Se debe establecer teniendo en cuenta el artculo 2 de la Resolucin 0811 de 2008, anexo tcnico nmero 2 en la cual se especifica que la localizacin de los puntos de recoleccin depende de su objetivo, si es para muestras de agua para consumo humano en la red de distribucin deber determinarse bajo las siguientes caractersticas:

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8
        LineaTiempoD.color-acento-contenido
          .row(numero="1" titulo="Determinar el punto en comn")
            .col-md-6.mb-4.mb-md-0
              p Determinar el punto en comn acuerdo entre las personas prestadoras y la respectiva autoridad sanitaria.
          
            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema3/img53.svg', alt='Imgen decorativa') 

          .row(numero="2" titulo="Establecer puntos fijos")
            .col-md-6.mb-4.mb-md-0
              p Inmediatamente despus del accesorio o componente donde terminan las tuberas de conduccin y se inicia la red de distribucin, en el extremo ms alejado de la red que sea ms representativo en calidad del agua y a la salida de la infraestructura ubicada en la red de distribucin que pueda representar un riesgo de contaminar el agua.
          
            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema3/img54.svg', alt='Imgen decorativa') 
                
          .row(numero="3" titulo="Establecer puntos de inters general")
            .col-md-6.mb-4.mb-md-0
              p Localizar puntos de muestreo teniendo en cuenta que representen el funcionamiento hidrulico del sistema.
          
            .col-md-6
              figure
                img(src='@/assets/curso/temas/tema3/img56.svg', alt='Imgen decorativa') 

      .col-10.col-lg-4(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema3/img57.svg', alt='Imgen decorativa')         

    .mb-5
      p Para muestreos que no son de agua para consumo humano siempre se recomienda:

    .row.justify-content-center.mb-5
      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img58.svg", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-3 Uno
            p.mb-0.text-center Identificar la zona previamente; si es posible georreferenciar la zona, indagar sobre el uso del recurso, la poblacin y situacin actual del mismo.

      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img59.svg", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-3 Dos
            p.mb-0.text-center Para aguas residuales, solicitar los permisos con anterioridad, verificar la zona y llevar los elementos de proteccin requeridos.

      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img60.svg", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-3 Tres
            p.mb-0.text-center Recuerde llevar la documentacin para registrar horas, fechas, lugares de la toma de muestras (Resolucin 0811, 2008).

    .row.bg1-tema3-v2.align-items-center.mb-5(data-aos="fade-right")
      h4.text-white.mt-2 Caractersticas de los puntos de muestreo:

    .row.justify-content-center.align-items-center.mb-5
      .col-1(data-aos="fade-down-right")
        figure
          img(src='@/assets/curso/temas/tema3/img61.svg', alt='Imgen decorativa')  
      .col-11(data-aos="fade-up-left")
        p La forma de tomar la muestra y el punto de muestreo est condicionado al lugar de este, por eso se debe considerar algunas particularidades a la hora de realizar una actividad de muestreo, para Ferro (2020), en su libro denominado Perito en salud medioambiental, segn las diferentes fuentes de agua las caractersticas del muestreo deben ser:

    TabsB.color-acento-contenido
      .py-4.py-md-5(titulo="Pozos de agua" :icono="require('@/assets/curso/temas/tema3/img62.svg')")(alt="Imgen decorativa")
        .row
          .col-md-7.mb-4.mb-md-0
            p Extraer la muestra de agua slo despus que el pozo ha sido bombeado por lo menos durante 15 minutos para asegurar que la muestra representa la calidad de la fuente de agua subterrnea.
        
          .col-md-5
            figure
              img(src='@/assets/curso/temas/tema3/img63.svg', alt='Imgen decorativa')

      .py-4.py-md-5(titulo="Ros y arroyos" :icono="require('@/assets/curso/temas/tema3/img64.svg')")(alt="Imgen decorativa")
        .row
          .col-md-7.mb-4.mb-md-0
            p Cuando se toman muestras de un ro o un arroyo, los valores analticos pueden variar con la profundidad, el caudal del arroyo y por la distancia a las orillas. Los cuidados a tener en cuenta en estos casos son: la muestra para que sea representativa debe ser recolectada a la mitad del rea del flujo, independientemente de la modalidad del muestreo, tener presente las inundaciones repentinas, seleccionar el punto de muestreo cercano a una estacin de aforo para relacionar el caudal del ro con la muestra de agua, en el caso de puntos de muestreo situados en las proximidades de confluencias y descargas, los puntos de muestreo debern estar ubicados a una distancia tal en que ambas aguas estn uniformemente mezcladas.
        
          .col-md-5
            figure
              img(src='@/assets/curso/temas/tema3/img65.svg', alt='Imgen decorativa')

      .py-4.py-md-5(titulo="Lagos y reservorios" :icono="require('@/assets/curso/temas/tema3/img66.svg')")(alt="Imgen decorativa")
        .row
          .col-md-7.mb-4.mb-md-0
